import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';

const routes: Routes = [
  {
    path: 't',
    component: TabsPage,
    children: [
      {
        path: '',
        redirectTo: '/t/ho-so',
        pathMatch: 'full'
      },
      {
        path: 'ho-so',
        loadChildren: () => import('../ho-so/ho-so.module').then(m => m.HoSoPageModule)
      },

      {
        path: 'cai-dat',
        loadChildren: () => import('../cai-dat/cai-dat.module').then(m => m.CaiDatPageModule)
      },
      {
        path: 'dong-gop',
        loadChildren: () => import('../dong-gop/dong-gop.module').then(m => m.DongGopPageModule)
      }
    ]
  },
  {
    path: '',
    redirectTo: '/t/ho-so',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
})
export class TabsPageRoutingModule { }
